import React from 'react';
import { connect } from "react-redux";

function Cart(props){
    return <table>
        <thead>
            <tr>
            <td>Item Name</td>
            <td></td>
            <td></td>
            </tr>
        </thead>
        <tbody>

            {
                props.cart.map(item => <tr>
                        <td>{ item.name }</td>
                        <td>{ item.rating }</td>
                        <td>
                            <button onClick={(e) => props.removeFromCart(item)}>
                                    Remove from Cart
                            </button>
                        </td>
                    </tr>
                )
            }

        </tbody>

    </table>
    

    
}


function mapStateToProps(state){
    return {
        cart: state.cart
    }
}

function mapDispatchToProps(dispatch){
    return {
        removeFromCart: (item) => {
            dispatch({
                type: "REMOVE",
                payload: item
            })
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Cart)